@import url(https://fonts.googleapis.com/css?family=Pacifico|Roboto);
html, body {
  margin: 0;
  padding: 0;
  width: 100vw;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #38b3be;
  overflow-x: hidden;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  height: 100%;
  min-height: 100vh;
  background-color: #1f1f1f;
}

.menu {
  height: 525px;
  display: flex;
}

h1 {
  font-size: 7em;
  margin-bottom: 25px;
  font-family: 'Pacifico', cursive;
  margin-top: 0;
  text-align: center;
}

h2 {
  font-size: 2.5em;
  margin-top: 50px;
  text-align: center;
  color: #55e888;
  font-family: 'Pacifico', cursive;
}

.loadTodos {
  font-size: 3em;
  background-color: #38b3be;
  color: #1f1f1f;
  border-radius: 25px;
  padding: 10px 25px;
  border: 5px solid #38b3be;
}

.loadTodos:hover {
  background-color: #1f1f1f;
  color: #38b3be;
  border: 5px solid #38b3be;
}

.listItem {
  color: #1f1f1f;
  width: 100vw;
  height: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2.5em;
  margin-top: 0;
  margin-bottom: 50px;
}

.listItem:hover {
  color: #2f2f2f;
  text-decoration: underline;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#textField {
  width: 500px;
  height: 50px;
  font-size: 2em;
  text-align: center;
  background-color: #1f1f1f;
  border: 3px solid #5f5f5f;
  border-radius: 25px;
  outline: none;
  margin-bottom: 25px;
  font-family: 'Roboto', sans-serif;
  padding: 2px;
}

#isBlue {
  width: 400px;
  height: 50px;
  font-size: 2em;
  background-color: #38b3be;
  border-radius: 25px;
  color: #1f1f1f;
  outline: none;
  border: none;
  font-family: 'Roboto', sans-serif;
}

#isBlue:hover {
  background-color: #1f1f1f;
  border: 3px solid #38b3be;
  color: #38b3be;
}

#isGreen {
  width: 400px;
  height: 50px;
  font-size: 2em;
  background-color: #55e888;
  border-radius: 25px;
  color: #1f1f1f;
  outline: none;
  border: none;
  font-family: 'Roboto', sans-serif;
}

#isGreen:hover {
  background-color: #1f1f1f;
  border: 3px solid #55e888;
  color: #55e888;
}

.accountForm {
  display: flex;
  flex-direction: column;
  text-align: center;
  color: #1f1f1f;
  background-color: #38b3be;
  border-radius: 25px;
  padding: 15px 35px;
  width: 250px;
  margin: 25px;
  font-size: 1.5em;
}

h3 {
  margin: 15px;
  font-size: 2em;
}

input {
  font-size: 0.9em;
  background-color: #1f1f1f;
  outline: none;
  border: none;
  border-radius: 15px;
  height: 50px;
  width: 225px;
  text-align: center;
  margin: 10px;
  color: #38b3be;
}

label {
  font-size: 0.75em;
}

.accountButton {
  border: 5px solid #1f1f1f;
  background-color: #38b3be;
  color: #1f1f1f;
  width: 240px;
}

.accountButton:hover {
  background-color: #1f1f1f;
  color: #38b3be;
}

nav {
  display: flex;
  justify-content: flex-end;
  margin-top: 50px;
  width: 90vw;
  font-size: 1.25em; 
}

nav p {
  margin: 10px;
}

nav p:hover {
  text-decoration: underline;
  color: #48c3de;
}

